import React from 'react';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../components/Layout';
import Banner from '../components/Banner';
import Container from '../components/Container';
import BlockHeaderNoImage from '../components/BlockHeaderNoImage';
import ACHReturnCodesTable from '../components/ACHReturnCodesTable';
import Footer from '../components/Footer';

export default function AchReturnCodesPage() {
  return (
    <Layout>
      <GatsbySeo
        title="Common ACH Return Codes [Examples & Definition]"
        description="Directory of the most common ACH Return Codes"
      />
      <Banner />
      <Container>
        <BlockHeaderNoImage
          title="What are ACH return codes?"
          desc={
            <p>
              ACH return codes are triggered when ACH payments are returned
              (payments failed to process or are reversed). ACH return codes can
              be caused by 85 different reasons during or even after ACH payment
              processing. Some of the ACH return codes aren't the most user
              friendly, and in some cases the ACH codes are reserved for use by
              government and/or receiving depository financial institution
              (RDFI) organizations.
              <br />
              <br />
              To simplify things we have compiled the list of 85 ACH return
              codes with a brief description of what each of them mean. At
              Resolve we aim to help B2B merchants grow their sales with
              automated financing and invoicing solutions.
            </p>
          }
          maxWidth="700"
          primaryButtonText="Get a Demo"
        />
        <ACHReturnCodesTable />
      </Container>
      <Footer darkSkin />
    </Layout>
  );
}
